	module.exports = {"data":{"site":{"siteMetadata":{"title":"Power Plays","author":"Salim Virani","description":"Exploring business models for energy distribution.","subscribeUrl":"http://sendy.source.institute/subscription?f=FAJQllsCGmgi20892763m1knaH0KxxIJW2PWQPF9D9hGhQc8s2Zg8927jte7Gwt5sKHWKWXL892Ln5o4RgRvHWsFxVf1iA"}},"markdownRemark":{"id":"/home/salim/code/powerplays.io/src/pages/why-energy-matters/index.md absPath of file >>> MarkdownRemark","html":"<p>About four years ago, I started to understand why a poor person might choose not to drink clean water, or not to use a toilet. It wasn’t a matter of education, or of access.</p>\n<p>It was after I’d spent a few months touring different humanitech startups, going into rural areas in Ghana, Uganda, Tanzania. Meeting customers and non-customers.</p>\n<p>I remember a lady explaining that the family’s budget goes towards saving for Typhoid meds, so auntie won’t die when it strikes again.  “To save money, we don’t buy drinking water.”</p>\n<p>I was optimistic about all the solar-powered, mobile-phone on-gridding happening for the poor, but whenever I traveled a few kilometres outside of a major city, I’d see it made little difference. </p>\n<p>The next year, Matt at Standard Microgrid explained his practical view - if people can’t afford basic amenities like clean water and sanitation, the way to enable them is economically.  Solar-powered reading lights won’t do that, but enough amperage to use a sewing machine, a power tool or a fridge will.</p>\n<p>I’m excited that in our lifetime, we may see the end of extreme poverty.  At the same time, our ecological problems are clear.  </p>\n<p>The way we currently distribute energy is ineffective. This is true whether you measure technical efficiency, or social equality. And underpinning both of these inefficiencies are the business models that drive energy companies.</p>\n<p>These business models are changing.</p>\n<p>Energy distribution is such an exciting topic, and I’m starting to explore it - technically, economically, socially.  This blog is where I plan to share what I learn.</p>","frontmatter":{"title":"Why energy matters to me","date":"June 07, 2018"}}},"pathContext":{"slug":"/why-energy-matters/","previous":null,"next":null}}

